.Editor__blockquote {
  border-left: 5px solid #eee;
  color: #666;
  font-family: "Hoefler Text", "Georgia", serif;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px;
}

.Editor__unstyled {
  margin: 8px 0;
  line-height: 1.8;
}

.Editor__list-item {
  margin: 4px 0;
}

pre[class*="language-"] .public-DraftStyleDefault-block {
  white-space: pre;
}